import React from 'react';
import styles from './QuizNumber.module.css';

const QuizNumber = ({number, isActiveNumber}) => {
    return (
        <div
            className={number === isActiveNumber ? styles.container : styles.containerDisabled}
        >
            <div
                className={number === isActiveNumber ? styles.quizNumber : styles.quizNumberDisabled}
            >
                <div className={styles.quizNumberText}>{number + 1}</div>
            </div>
        </div>
    );
};

export default QuizNumber;