import React from 'react';
import styles from "./QuizNumberContainer.module.css";
import QuizNumber from "../QuizNumber/QuizNumber";

const QuizNumberContainer = ({list, isActiveNumber, ref, scrollLeft}) => {
    return (
        <div className={styles.quizNumberContainer} ref={ref} style={{transform: `translateX(-${scrollLeft}px)`}}>
            {list.map((el, index) => {
                return <QuizNumber
                    key={index}
                    number={index}
                    isActiveNumber={isActiveNumber}
                />
            })}
        </div>
    );
};

export default QuizNumberContainer;