import React from 'react';
import styles from './QuizContainer.module.css';

const QuizContainer = ({item, widthContent, answerHandler, isCheckedNumber, setIsCheckedNumber}) => {
    let alphabet = 'abcdefghijklmnopqrstuvwxyz'.toUpperCase().split('');

    let selectAnswer = (index, answer, question) => {
        setIsCheckedNumber(index);
        answerHandler([question, answer]);
    }

    return (
        <div className={styles.quizContainer} style={{width: `${widthContent}px`}}>
            <div className={styles.quizQuestion}>{item.question}</div>
            <div className={styles.quizAnswers}>
                {item.answers.map((answer, index) => {
                    return (
                        <div className={styles.quizAnswer} key={index} onClick={() => selectAnswer(index, answer, item.question)}>
                            <div className={isCheckedNumber === index ? styles.checkbox : styles.checkboxDisabled}>
                                <span>{alphabet[index]}</span>
                            </div>
                            <div className={styles.answer}>{answer}</div>
                        </div>
                    )
                })}
            </div>
        </div>
    );
};

export default QuizContainer;