import React from 'react';
import styles from "./Quiz.module.css";
import QuizNumberContainer from "../QuizNumberContainer/QuizNumberContainer";
import QuizContainer from "../QuizContainer/QuizContainer";
import QuizButtons from "../QuizButtons/QuizButtons";
import {useWindowSize} from "../../hooks/useWindowSize";

const Quiz = ({quizName, list, request, width}) => {
    let [isActiveNumber, setIsActiveNumber] = React.useState(0);
    let [heightHeader, setHeightHeader] = React.useState(0);
    let [scrollLeft, setScrollLeft] = React.useState(0);
    let [scrollLeftContent, setScrollLeftContent] = React.useState(0);
    let [widthContent, setWidthContent] = React.useState(0);
    let [isCheckedNumber, setIsCheckedNumber] = React.useState(-1);
    let [isAnswers, setIsAnswers] = React.useState([]);
    let [isFinish, setIsFinish] = React.useState(false);

    let ref = React.useRef(null);
    let ref1 = React.useRef(null);
    let ref2 = React.useRef(null);
    let ref3 = React.useRef(null);
    let ref4 = React.useRef(null);
    // let ref5 = React.useRef(null);

    // let size = useWindowSize()
    let size = width

    React.useEffect(() => {
        setIsAnswers(generateArrAnswers)
    }, [])

    React.useEffect(() => {
       setHeightHeader(ref3.current.offsetHeight);
        let widthContainer = ref.current.offsetWidth;
        let widthElement = widthContainer / list.length;
        let widthQuiz = ref1.current.offsetWidth - 10;
        let maxScrollNumber = list.length - Math.floor(widthQuiz / widthElement);
        if(maxScrollNumber <= 0) {
            ref2.current.style.width = `${widthContainer}px`;
            ref4.current.style.width = `${widthContainer * list.length}px`
            setWidthContent(widthContainer);
            ref2.current.style.overflow = "none";
            return
        }
        ref2.current.style.width = `${Math.floor(widthQuiz / Math.floor(widthElement)) * widthElement}px`;
        ref4.current.style.width = `${widthContainer * list.length}px`
        setWidthContent(widthQuiz);
        ref2.current.style.overflow = "hidden";
    }, [useWindowSize()])

    React.useEffect(() => {
        if(isActiveNumber === list.length - 1 && isAnswers[list.length - 1].answer !== '' ) {
            setIsFinish(true)
        }
    }, [isAnswers])

    let scrollToElement = () => {
        let widthContainer = ref.current.offsetWidth;
        let widthElement = widthContainer / list.length;
        let widthQuiz = ref1.current.offsetWidth - 10;
        let maxScrollNumber = list.length - Math.floor(widthQuiz / widthElement);
        if(widthQuiz > widthContainer) {
            setScrollLeftContent(prevState => prevState + widthContainer)
            return;
        }
        if(widthElement * maxScrollNumber <= scrollLeft) {
            setScrollLeftContent(prevState => prevState + widthQuiz)
            return
        }
        setScrollLeft(prevState => prevState + widthElement)
        setScrollLeftContent(prevState => prevState + widthQuiz)
    }

    let activeNumberHandler = () => {
        if(isActiveNumber === list.length - 1) {
            return
        }
        if(isCheckedNumber === -1) {
            return;
        }
        setIsActiveNumber(isActiveNumber + 1);
        scrollToElement()
        setIsCheckedNumber(-1)
    }

    let generateArrAnswers = () => {
        let arrAnswers = [];
        for(let i = 0; i < list.length; i++) {
            arrAnswers.push({question: list[i].question, answer: ""})
        }
        return arrAnswers;
    }

    let answerHandler = (data) => {
        setIsAnswers([...isAnswers.filter(el => el.question !== data[0]), {question: data[0], answer: data[1]}])
    }

    // React.useEffect(() => {
    //     let sendData = async () => {
    //         ref5.current.
    //     }
    // }, [isFinish])
    let sendData = async () => {
        const response = await request('answer', 'post', {data: isAnswers})
        // invoice.open(response.invoice_link.replace("https://t.me/$", ""))
        console.log(JSON.stringify({data: isAnswers}));
    }


    return (
        <div className={styles.quiz} ref={ref1}>
            <div className={styles.quizHeader} ref={ref3} style={{top: `-${heightHeader + 5}px`}}>
                {quizName}
            </div>
            <div className={styles.quizWrapper} ref={ref2}>
                <QuizNumberContainer list={list} isActiveNumber={isActiveNumber} ref={ref} scrollLeft={scrollLeft}/>
                <div className={styles.quizContentWrapper} ref={ref4} style={{transform: `translateX(-${scrollLeftContent}px)`}}>
                    {list.map((el, index) => {
                        return <QuizContainer
                            item={list[index]}
                            key={index}
                            widthContent={widthContent}
                            answerHandler={answerHandler}
                            isCheckedNumber={isCheckedNumber}
                            setIsCheckedNumber={setIsCheckedNumber}
                        />
                    })}
                </div>
                <QuizButtons
                    isFinish={isFinish}
                    activeNumberHandler={activeNumberHandler}
                   sendData={sendData}
                />
            </div>
        </div>
    );
};

export default Quiz;